'use client'

import { createContext, useContext, useState, useEffect, ReactNode, useCallback } from 'react'
import { api } from './api-client'
import { toast } from '@/hooks/use-toast'

interface AuthTokens {
  access_token: string
  refresh_token: string
  expires_in: number
}

interface AuthContextType {
  isAuthenticated: boolean
  isLoading: boolean
  login: (email: string, password: string, rememberMe?: boolean) => Promise<void>
  register: (data: RegisterData) => Promise<void>
  logout: () => Promise<void>
  checkEmailAvailability: (email: string) => Promise<boolean>
  checkUsernameAvailability: (username: string) => Promise<boolean>
  openAuthModal: (tab?: 'login' | 'register') => void
  closeAuthModal: () => void
  isAuthModalOpen: boolean
  authModalTab: 'login' | 'register'
}

interface RegisterData {
  username: string
  email: string
  password: string
  currency?: string
  country?: string
  lang?: string
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [isLoading, setIsLoading] = useState(true)
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false)
  const [authModalTab, setAuthModalTab] = useState<'login' | 'register'>('login')

  // Check if user is authenticated on mount
  useEffect(() => {
    const accessToken = localStorage.getItem('access_token')
    const refreshToken = localStorage.getItem('refresh_token')
    
    if (accessToken && refreshToken) {
      setIsAuthenticated(true)
    }
    
    setIsLoading(false)
  }, [])

  const openAuthModal = useCallback((tab: 'login' | 'register' = 'login') => {
    setAuthModalTab(tab)
    setIsAuthModalOpen(true)
  }, [])

  const closeAuthModal = useCallback(() => {
    setIsAuthModalOpen(false)
  }, [])

  const login = async (email: string, password: string, rememberMe: boolean = false) => {
    try {
      const response = await api.auth.login({ email, password })
      
      if (response.success && response.data) {
        const { access_token, refresh_token, player } = response.data
        
        // Store tokens
        localStorage.setItem('access_token', access_token)
        localStorage.setItem('refresh_token', refresh_token)
        
        // Store player ID for profile fetching
        localStorage.setItem('player_id', player.id)
        
        if (rememberMe) {
          localStorage.setItem('remember_me', 'true')
        }
        
        setIsAuthenticated(true)
        closeAuthModal()
        
        toast({
          title: 'Welcome back!',
          description: `Successfully logged in as ${player.username}`,
        })
      } else {
        throw new Error(response.error?.message || 'Login failed')
      }
    } catch (error: any) {
      console.error('Login error:', error)
      const errorMessage = 
        error.response?.data?.error?.message || 
        error.message || 
        'Invalid email or password'
      
      toast({
        title: 'Login failed',
        description: errorMessage,
        variant: 'destructive',
      })
      throw error
    }
  }

  const register = async (data: RegisterData) => {
    try {
      const response = await api.auth.register(data)
      
      if (response.success && response.data) {
        const { access_token, refresh_token, player } = response.data
        
        // Store tokens
        localStorage.setItem('access_token', access_token)
        localStorage.setItem('refresh_token', refresh_token)
        
        // Store player ID for profile fetching
        localStorage.setItem('player_id', player.id)
        
        setIsAuthenticated(true)
        closeAuthModal()
        
        toast({
          title: 'Welcome to Nest Casino!',
          description: `Account created successfully. Welcome, ${player.username}!`,
        })
      } else {
        throw new Error(response.error?.message || 'Registration failed')
      }
    } catch (error: any) {
      console.error('Registration error:', error)
      const errorMessage = 
        error.response?.data?.error?.message || 
        error.message || 
        'Registration failed. Please try again.'
      
      toast({
        title: 'Registration failed',
        description: errorMessage,
        variant: 'destructive',
      })
      throw error
    }
  }

  const logout = async () => {
    try {
      const accessToken = localStorage.getItem('access_token')
      
      if (accessToken) {
        // Call logout endpoint to blacklist token
        await api.auth.logout(accessToken)
      }
    } catch (error) {
      console.error('Logout error:', error)
      // Continue with logout even if API call fails
    } finally {
      // Clear tokens and state
      localStorage.removeItem('access_token')
      localStorage.removeItem('refresh_token')
      localStorage.removeItem('player_id')
      localStorage.removeItem('remember_me')
      
      setIsAuthenticated(false)
      
      toast({
        title: 'Logged out',
        description: 'You have been successfully logged out',
      })
    }
  }

  const checkEmailAvailability = async (email: string): Promise<boolean> => {
    try {
      const response = await api.players.checkEmail(email)
      // exists: true means email is taken (not available)
      return !response.data.exists
    } catch (error) {
      console.error('Email check error:', error)
      return true // Assume available on error
    }
  }

  const checkUsernameAvailability = async (username: string): Promise<boolean> => {
    try {
      const response = await api.players.checkUsername(username)
      // exists: true means username is taken (not available)
      return !response.data.exists
    } catch (error) {
      console.error('Username check error:', error)
      return true // Assume available on error
    }
  }

  return (
    <AuthContext.Provider
      value={{
        isAuthenticated,
        isLoading,
        login,
        register,
        logout,
        checkEmailAvailability,
        checkUsernameAvailability,
        openAuthModal,
        closeAuthModal,
        isAuthModalOpen,
        authModalTab,
      }}
    >
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}

